.leaderboard-stat {
  color: black;
  width: 100%;
  height: 65px;
  border: black solid 0.1px;
  background-color: white;
  text-align: center;
}

.stats-container {
  color: white;
  margin-left: 5%;
  margin-right: 5%;
  margin-bottom: 2%;
}

.leaderboard-table {
  width: 100%;
  border-collapse: collapse;
}

tr:nth-child(even) {
  background-color: #dddddd;
}

tr:nth-child(1) {
  font-family: "Permanent Marker", cursive;
  font-weight: 400;
}

.loading-btn-div {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
