.schedule-title {
  color: black;
}

.schedule-container {
  min-width: 100%;
  min-height: 250px;
  color: black;
}

.schedule-h3 {
  color: red;
  text-align: center;
  font-size: 18px;
}

.class-link {
  text-align: center;
  margin-top: 4%;
  margin-left: auto;
  margin-right: auto;
  display: block;
  color: white;
  border-color: white;
  background-color: rgb(3, 151, 250);
  width: 180px;
  height: 35px;
  border-radius: 15px;
  font-family: "Permanent Marker", cursive;
  font-size: 15px;
  transition: all 0.2s ease-in-out;
}

.class-link:hover {
  background-color: lightslategray;
  border-radius: 10px;
}
